@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#fp-nav ul li:hover a.active span,
#fp-nav ul li a.active span,
.fp-slidesNav ul li:hover a.active span,
.fp-slidesNav ul li a.active span {
  background-color: white;
}
#fp-nav ul li:hover a span,
#fp-nav ul li a span,
.fp-slidesNav ul li:hover a span,
.fp-slidesNav ul li a  span {
  background-color: black;

}

body {
  background: #222;
  font-family: "Roboto Mono", monospace;
  font-size: 9px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #e6e7e8;
  overflow: hidden;
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.inputs {
  background: rgba(255, 255, 255, 0.03);
  padding: 10px;
  margin-top: 10px;
  border: 1px dashed #555;
  flex-direction: column;
  align-items: center;
  display: flex;
  justify-content: center;
  -webkit-transition: opacity 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
  transition: opacity 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
  transition: transform 300ms ease-in-out, opacity 300ms ease-in-out;
  transition: transform 300ms ease-in-out, opacity 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
}
.inputs.hide {
  -webkit-transform: translateX(60%);
          transform: translateX(60%);
  opacity: 0;
  pointer-events: none;
}
.inputs > div {
  width: 100%;
  margin-bottom: 5px;
}
.inputs > div:last-of-type {
  margin: 0;
}
.inputs input {
  cursor: pointer;
}
.inputs input[type=range] {
  width: 110px;
}
.inputs label {
  cursor: pointer;
}
.inputs p {
  margin: 0;
}

button {
  cursor: pointer;
  font-family: "Roboto Mono", monospace;
  -webkit-appearance: none;
  background: #e6e7e8;
  border: none;
  outline: none;
  font-size: 10px;
  padding: 5px 10px;
  color: #222;
  min-width: 100%;
  border-radius: 2px;
  -webkit-transition: background 100ms;
  transition: background 100ms;
}
button:hover {
  background: #ccc;
}
button:active {
  background: #aaa;
}

.panel {
  position: absolute;
  top: 10px;
  right: 10px;
}

.container {
  -webkit-filter: blur(1px);
          filter: blur(1px);
  position: relative;
  width: 300px;
  height: 300px;
  -webkit-transition: -webkit-perspective 300ms ease;
  transition: -webkit-perspective 300ms ease;
  transition: perspective 300ms ease;
  transition: perspective 300ms ease, -webkit-perspective 300ms ease;
}

.cube {
  height: 100%;
  width: 100%;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: -webkit-transform 300ms ease;
  transition: -webkit-transform 300ms ease;
  transition: transform 300ms ease;
  transition: transform 300ms ease, -webkit-transform 300ms ease;
}
.cube.animated {
  -webkit-transition-duration: 100ms;
          transition-duration: 100ms;
}
.cube figure {
  position: absolute;
  margin: 0;
  height: 300px;
  width: 300px;
  background: #fff;
  display: block;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.cube figure:nth-of-type(1) {
  background: #DB5461;
  -webkit-transform: translateZ(150px);
          transform: translateZ(150px);
}
.cube figure:nth-of-type(2) {
  background: #DB5461;
  -webkit-transform: rotateX(180deg) translateZ(150px);
          transform: rotateX(180deg) translateZ(150px);
}
.cube figure:nth-of-type(3) {
  background: #593C8F;
  -webkit-transform: rotateY(-90deg) translateZ(150px);
          transform: rotateY(-90deg) translateZ(150px);
}
.cube figure:nth-of-type(4) {
  background: #593C8F;
  -webkit-transform: rotateY(90deg) translateZ(150px);
          transform: rotateY(90deg) translateZ(150px);
}
.cube figure:nth-of-type(5) {
  background: #8EF9F3;
  -webkit-transform: rotateX(90deg) translateZ(150px);
          transform: rotateX(90deg) translateZ(150px);
}
.cube figure:nth-of-type(6) {
  background: #8EF9F3;
  -webkit-transform: rotateX(-90deg) translateZ(150px);
          transform: rotateX(-90deg) translateZ(150px);
}

@-webkit-keyframes animateCube {
  0% {
    -webkit-transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
            transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
  }
  100% {
    -webkit-transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
            transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
  }
}

@keyframes animateCube {
  0% {
    -webkit-transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
            transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
  }
  100% {
    -webkit-transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
            transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
  }
}

